import './App.css';
import Events from './components/Events';
import Home from './components/Home';
import ListOfVideos from './components/ListOfVideos';
import NavBar from './components/NavBar';
import Videos from './components/Videos';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { useEffect,useState } from 'react';
import Footer from './components/Footer';
import PlaylistOne from './components/PlaylistOne';
import PlayList2 from './components/PlayList2';
import PlayList3 from './components/PlayList3';
import Contact from './components/Contact';

function App() {
  const [flowers, setFlowers] = useState([]);

  // Function to generate random flowers
  const generateFlower = () => {
    const left = Math.random() * window.innerWidth;
    const rotation = Math.random() * 360;
    return { left, rotation };
  };

  // Function to add a new flower to the animation
  const addFlower = () => {
    const newFlower = generateFlower();
    setFlowers((prevFlowers) => [...prevFlowers, newFlower]);
  };

  // Automatically add a flower every 1 second
  useEffect(() => {
    const interval = setInterval(addFlower, 1000);
    return () => clearInterval(interval);
  }, []);
  
  return (
    <>
    
    
    <NavBar/>
    <Home/>
   
    <Events/>
    <Router>
    
    <ListOfVideos/>
    
    
      <Switch>
      
        <Route exact path="/videoMain" ><Videos/></Route>
        <Route exact path="/playlist1" ><PlaylistOne/></Route>
        <Route exact path="/playlist2" ><PlayList2/></Route>
        <Route exact path="/playlist3" ><PlayList3/></Route>
       
      </Switch>
    </Router>
    <Contact/>
   
    <Footer/>
   </>
  );
}

export default App;
